.header{
    width: 100%;
    height: 45px;
    background: #49ced4;
    padding: 10px 15px;
    display: flex;
    align-items: center;
}
.header-left{
    display: flex;
    align-items: center;
    color: #fff;
}
.header-left>span{
    margin-left: 2px;
}
.header-arrow-left{
    width: 24px;
    height: 24px;
    background-image: url('../images/titlebar_back@2x.png');
    background-size: 100%;
    background-repeat: no-repeat;
}
.header-title{
    color: #fff;
    margin: 0px auto;
}
.header-avat{
    width: 24px;
    height: 24px;
    background: #fff;
    border-radius: 50%;
}
.swiper-container{
    width: 100%;
    height: auto;
}
.swiper-container-horizontal>.swiper-pagination-bullets, .swiper-pagination-custom, .swiper-pagination-fraction{
    width: auto;
    right: 10px;
    left: auto;
    bottom: 8px;
}
.swiper-pagination-bullet{
    width: 6px;
    height: 6px;
    background: #eee;
}
.swiper-pagination-bullet-active{
    background: #fff;
}
.swiper-slide img{
    width: 100%;
}
.banner-tags{
    position: absolute;
    left: 0px;
    top: 0px;
    display: flex;
    flex-flow: wrap;
}
.banner-tag{
    color: #fff;
    font-size: 1.2rem;
    margin: 5px 5px 0px 10px;
}
.banner-title-wrapper{
    width: 100%;
    height: 32px;
    background: #000;
    line-height: 32px;
    position: absolute;
    bottom: 0px;
    left: 0px;
    z-index: 1000;
    opacity: 0.3;
}
.banner-title{
    width: calc( 100% - 60px );
    height: 32px;
    line-height: 32px;
    font-size: 1.4rem;
    position: absolute;
    bottom: 0px;
    left: 0px;
    z-index: 1001;
    color: #fff;
    padding-left: 10px;
}
.databar{
    width: 100%;
    height: 40px;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
    padding: 10px 15px;
    display: flex;
    align-items: center;
    color: #4d6983;
}
.platebar{
    width: 100%;
    height: 40px;
    border-top: 1px solid #ddd;
    padding: 10px 15px;
    display: flex;
    align-items: center;
    color: #4d6983;
}
.plate-select-area{
    width: 100%;
    height: calc( 100vh - 85px );
    position: fixed;
    z-index: 1000;
    top: 85px;
    left: 0px;
    overflow-y: auto;
}
.plate-bg{
    width: 100%;
    height: 100%;
    background: #000;
    opacity: 0.6;
    z-index: 1001;
}
.plate-select-content{
    width: 100%;
    padding: 10px 15px;
    background: #f5f5f5;
    position: absolute;
    top: 0;
    z-index: 1002;
    color: #4d6983;
}
.plate-name{
    font-weight: 600;
}
.plate-childs{
    margin: 10px 0px;
    display: flex;
    flex-wrap: wrap;
    font-size: 1.4rem;
}
.plate-childs-item{
    min-width: 80px;
    text-align: center;
    padding: 5px 10px;
    background: #fff;
    border-radius: 4px;
    margin: 0px 10px 10px 0px;
}
.bar-label{
    font-weight: 600;
}
#data-area{
    width: 100%;
    height: 300px;
    padding: 10px 15px;
}
/*.datashow-enter-active{
    animation: slidein .3s;
}
.datashow-leave-active{
    animation: slideout .3s;
}*/
@keyframes slideout{
    from{ 
        width: 300;
    }
    to{
        height: 0;
    }
}
@keyframes slidein{
    from{ 
        width: 0;
    }
    to{
        height: 300;
    }
}
.page-bar{
    height: 40px;
    display: flex;
    align-items: center;
    border-top: 1px solid #ddd;
    border-bottom: 1px solid #ddd;
}
.tab-wrapper{
    width: calc( 100% - 80px );
    display: flex;
    align-items: center;
}
.page-item{
    width: 50%;
    padding: 9px 15px;
    text-align: center;
}
.page-item-cur{
    border-bottom: 2px solid #49ced4;
}
.page-filter{
    width: 80px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-left: 1px solid #ddd;
    cursor: pointer;
}
.page-filter>span{
    margin-right: 5px;
}
.page-filter-area{
    width: 100vw;
    height: 100vh;
    position: fixed;
    top: 0px;
    left: 0px;
    z-index: 1000;
}
.page-filter-bg{
    width: 100%;
    height: 100%;
    background: #000;
    opacity: 0.6;
    z-index: 1000;
}
.page-filter-content{
    width: 78%;
    height: calc( 100% - 44px );
    position: absolute;
    right: 0px;
    top: 0px;
    z-index: 1001;
    background: #fff;
    padding: 10px 15px;
    overflow-y: auto;
}
.page-name{
    font-weight: 600;
}
.page-childs{
    margin: 10px 0px;
    display: flex;
    flex-wrap: wrap;
    font-size: 1.4rem;
}
.page-childs-item{
    min-width: 60px;
    text-align: center;
    padding: 5px 10px;
    background: #f5f5f5;
    border-radius: 4px;
    margin: 0px 10px 10px 0px;
}
.page-filter-btns{
    width: 78%;
    height: 44px;
    position: absolute;
    bottom: 0;
    right: 0;
    z-index: 1001;
    display: flex;
}
.page-filter-btns>button{
    width: 50%;
    border-radius: 0px;
    font-size: 1.4rem;
}
.article-item{
    padding: 10px 15px;
    border-bottom: 1px solid #ddd;
    color: #4d6983;
    display: block;
}
.article-title{
    font-weight: bold;
    font-size: 1.4rem;
}
.article-info{
    display: flex;
    font-size: 1.2rem;
    color: #8e9eaf;
    margin-top: 10px;
}
.article-tags{
    width: calc( 100% - 100px );
    display: flex;
    flex-wrap: wrap;
}
.article-tag{
    margin-right: 10px;
}
.article-time{
    width: 100px;
    text-align: right;
    margin-left: auto;
}
.article-more{
    height: 40px;
    line-height: 40px;
    text-align: center;
    font-size: 1.4rem;
    color: #4d6983;
}
.article-user-info{
    display: flex;
    font-size: 1.2rem;
    color: #8e9eaf;
    margin-top: 10px;
}
.article-user-tags{
    width: 100px;
}
.article-user-tags{
    width: calc( 100% - 100px );
    display: flex;
    flex-wrap: wrap;
    margin-left: auto;
}
.bussines-container{
    width: 100%;
    height: 600px;
    background: #eef1f1;
}
.qrcode-list{
    padding: 40px 50px 20px 50px;
}
.qrcode-list img{
    width: 31%;
    display: inline-block;
}
.partner-container{
    padding: 10px 15px;
}
.partner-list, .media-list{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}
.partner-title, .medias-title, .contact-title{
    font-weight: bold;
    color: #474948;
    margin-top: 30px;
}
.partner-list img{
    padding: 5px;
    width: 30%;
    margin-top: 10px;
    display: inline-block;
}
.media-list img{
    width: 50%;
    margin-top: 20px;
    display: inline-block;
}
.contact-info{
    font-size: 12px;
    color: #474948;
    margin-top: 10px;
}
.join-us{
    margin-top: 50px;
    margin-bottom: 20px;
    font-size: 1.2rem;
    color: #474948;
}
.article-new-btn{
    width: 58px;
    height: 58px;
    background-image: url('../images/fatie_btn@2x.png');
    background-size: 100%;
    display: inline-block;
    background-repeat: no-repeat;
    position: fixed;
    right: 20px;
    bottom: 25px;
    z-index: 999;
}


